html, body, #root, .App {
  width: 100%;
  height: 100%;
}

#root > div{
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: 100%;
}

#root > div > div:nth-child(1) > nav.navbar.bg-light.\.justify-content-center{
  border-bottom: 1px solid black;
}

#collapseExample > input {
  text-align: center;
}

#root > div > header > nav{
  width: 100%;
}
#root > div > header > div {
  width: 100%;
}
#root > div > div:nth-child(1) > nav.navbar.bg-light.\.justify-content-center > a{
  margin: 0 auto;
  color: black;
  font-family: 'Russo One', sans-serif;
}
#root > div > div:nth-child(1) > nav.navbar.bg-light.\.justify-content-center > a > p > span{
  color: rgb(15, 104, 77);
}
#root > div > div:nth-child(1) > nav.navbar.bg-light.\.justify-content-center > a > p {
  font-size: 25px;
}

.margin0a {
  margin: 0 auto;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  margin-top: 0;
}

#navbarNavDropdown > ul > li:nth-child(4) > a > img {
  margin: auto;
  width: 4vh;
  height: 4vh;
  background: blue;
  border-radius: 50%;
}
#featuredProject {
  max-width: 400px;
}

.mainContent{
  padding-bottom: 2em;
}

#root >  div > div.mainContent.container-fluid > div{
  justify-content: space-evenly;
}

#recommendation-list > ul > li > div > div:nth-child(1) > img {
   max-width: 7em;
   text-align: center;
}


#recommendation-list > ul {
  color: black;
}
#recommendation-list > ul > li > div{
  display: inline-flex;
}

#recommendation-list > ul > li > div > div:nth-child(2){
  padding-left: 20px;
}

#featuredProject{
  /* grid-column: 2;
  grid-row: 2; */
  /* margin: 0 auto; */
  background: white;
  color: black;
}

#recommendation-list {
  /* grid-row: 2;
  grid-column: 3; */
  text-align: left;
  min-height: 250px;
}


#root > div > div.footer > footer{
  background: lightgrey;
  border-bottom: 1px solid white;
  
}

#root > div > div.footer > footer > div.footer-copyright.text-center.py-3 {
  background: lightgrey;
}

#root > div > div.footer > footer > div.container.text-center.text-md-left > div > div:nth-child(n+1) {
  border-left: 1px solid white;
  border-right: 1px solid white;
}

/* Project Page */
#root > div > div > div.projectInfo > div:nth-child(4) {
  border: 0.5px solid black;
}

#projCTA{
  border: 2px solid black;
  margin: 0 auto;
}

#progress{
  width: 67%;
  height: 1em;
  background: green;
}

#info{
  padding: 2em;
  text-align: left;
}

#featuredProject > ul > li > div > div.row.align-text-center.justify-content-center, #info > div{
  justify-content: space-evenly!important;
}
/* Project Info type nav */
#navbarNavDropdown > ul {
  justify-content: space-around;
}

/* Form style */
form label, .form input {
  display: block;
}

form input {
  margin-bottom: 1rem;
}

form label {
  margin-bottom: 0.2rem;
}

form input[type="text"],
form input[type="email"],
form input[type="password"] {
  width: 100%;
  padding: 0.5rem;
  font-size: 1.1rem;
}

form input:focus {
  border: 1px solid #256ee4;
  box-shadow: inset 0px 0px 5px #5a7eb8;
  outline: none;
}

.tableDiv{
  overflow-x: auto;
}

/* Project Page */
#carouselExampleControls > div {
  background: #dcdcdc;
  border: 2px solid #dcdcdc;
  border-radius: 25px;
}

@media screen(max-width: 300px){
  html {
    background: "red";
  }
}

/* Project Carousel */

#projectCard{
  width: 18rem;
  margin: 0 auto;
  /* max-height: 400px; */
  overflow-x: hidden;
}

#projectCarousel{
  /* cursor: grab; */
  display: inline-flex;
  overflow-x: scroll;
  max-width: 100%;
}

#otherProjects .card{
  min-width: 20rem;
} 

/* Crypto Card */

.cryptoCardImg {
  max-height: 10rem;
  width: auto;
}

@media (min-width: 768px){
.cryptoCardImg > img {
  height: 100%!important;
  width: auto!important;
}}

  .cryptoCardImg > img {
    height: 5rem;
    width: auto;
  }

#root > div > div:nth-child(2) > form > div:nth-child(1) > div > div > div.card-body {
  padding: 0;
}

.card .cryptoCard:not(.selected) {
  opacity: 0.5;
}

.tokenIcon{
  max-height: 45px;
}

.iconCell{
  padding: 0!important;
  display: grid;
  place-items: center!important;
}

.founderImg{
  width: 250px;
  height: 250px;
  object-fit: cover;
}

.centerCards{
  display: inline-flex;
  justify-content: space-evenly;
}

.founderCard{
  max-width: 250px;
}

.list-group-item{
  padding: 0.75rem 0 0.75rem 0;
}